// Fonts
$primary-font: "Cinzel", serif;
$secondary-font: "Della Respira", serif;

// Colors
$clr-primary-1: hsl(21, 91%, 17%);
$clr-primary-2: hsl(21, 84%, 25%);
$clr-primary-3: hsl(21, 81%, 29%);
$clr-primary-4: hsl(21, 77%, 34%);
$clr-primary-5: hsl(21, 62%, 45%);
$clr-primary-6: hsl(21, 57%, 50%);
$clr-primary-7: hsl(21, 65%, 59%);
$clr-primary-9: hsl(21, 94%, 87%);
$clr-primary-10: hsl(21, 100%, 94%);
$clr-grey-1: hsl(209, 61%, 16%);
$clr-grey-2: hsl(211, 39%, 23%);
$clr-grey-3: hsl(209, 34%, 30%);
$clr-grey-4: hsl(209, 28%, 39%);
$clr-grey-5: hsl(210, 22%, 49%);
$clr-grey-6: hsl(209, 23%, 60%);
$clr-grey-7: hsl(211, 27%, 70%);
$clr-grey-9: hsl(212, 33%, 89%);
$clr-grey-8: hsl(210, 31%, 80%);

// Colors Stoa
$primary_color: rgb(250, 224, 182);
$secondary_color: rgba(250, 224, 182, 0.787);
$sec_color-300: rgba(250, 224, 182, 0.301);
$sec_color-500: rgba(250, 224, 182, 0.5);
$gold: rgb(255, 188, 2);
$gold-350: rgba(255, 188, 2, 0.35);
$gold-700: rgba(255, 188, 2, 0.7);
$gold-750: rgba(255, 188, 2, 0.75);
$white: rgb(255, 255, 255);
$black: rgb(0, 0, 0);
$black-850: rgba(0, 0, 0, 0.856);
$brown: rgb(119, 67, 35);
$brown_bg: rgba(119, 67, 35, 0.897);
$brown_one: rgb(64, 63, 68);
$brown_two: rgb(151, 55, 14);
$bg_black: rgb(17, 11, 11);
$bg_dark: rgb(18, 19, 19);
$nav_bg_color: rgba(18, 19, 19, 0.6);
$menu_bg_color: rgba(18, 19, 19, 0.9);
$footer_bg_color: rgba(26, 25, 23, 0.95);
$footer_bg_color_two: rgba(34, 32, 33, 0.95);
$footer_bg_color_three: rgba(22, 26, 27, 0.97);

// Tools
$transition: all 0.3s linear;
$nav-shadow: 1px 3px 6px rgba(black, 0.5);

// Media quarries Max-Width
@mixin m_q($size) {
  @media screen and (max-width: $size) {
    @content;
  }
}
